body {
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5; /* Light Gray */
  color: #333; /* Dark Gray */
  margin: 0;
  padding: 0;
}

.otaku-app-container,
.otaku-chapter-container,
.otaku-mangapage-container {
  font-family: 'Arial', sans-serif;
  background-color: #fff; /* White */
  color: #333; /* Dark Gray */
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

/* Title Style */
.otaku-title {
  font-size: 28px;
  font-weight: bold;
  color: #333; /* Dark Gray */
}

/* Search Container */
.otaku-search-container {
  text-align: center;
  margin: 20px;
}

/* Input and Select Styles */
.otaku-input,
.otaku-select {
  padding: 14px;
  font-size: 18px;
  border: 2px solid #3498db; /* Dodger Blue */
  background-color: #3498db; /* Dodger Blue */
  color: #ecf0f1; /* Clouds */
  border-radius: 8px;
  margin-right: 10px;
  margin-bottom: 20px;
}

/* Buttons Styles */
.otaku-button,
.otaku-fetch-button {
  padding: 14px 28px;
  font-size: 18px;
  background-color: #3498db; /* Dodger Blue */
  border: 2px solid #3498db; /* Dodger Blue */
  color: #ecf0f1; /* Clouds */
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: background-color 0.3s ease;
}

.otaku-button:hover,
.otaku-fetch-button:hover {
  background-color: #2980b9; /* Belize Hole */
}

/* Results and Manga Page Containers */
.otaku-results,
.otaku-manga-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Manga Item and Page Button Styles */
.otaku-manga-item,
.otaku-manga-page-button {
  margin: 10px;
  text-align: center;
  font-size: 16px;
}

/* Manga Image and Page Image Styles */
.otaku-manga-image,
.otaku-manga-page-img {
  width: 100%;
  max-width: 600px;
  height: auto;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

/* Selected Chapter Style */
.otaku-selected-chapter {
  margin-top: 10px;
}

/* Dropdown Styles */
.otaku-dropdown {
  width: 300px;
}

.otaku-dropdown-option {
  font-size: 16px;
  color: #333;
}

.otaku-dropdown-option:hover {
  background-color: #e74c3c; /* Alizarin */
  color: #ecf0f1;
}

/* Dropdown No Results and Placeholder Styles */
.otaku-dropdown-no-results,
.otaku-dropdown-placeholder {
  color: #e74c3c; /* Alizarin */
}

/* Search Input Style */
.otaku-search-input {
  width: 300px;
  margin-top: 10px;
}

/* Dark Mode Styles */
.otaku-dark-mode {
  background-color: #fff; /* White */
  color: #333; /* Dark Gray */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.search-title {
  font-family: 'OtakuFont';
  margin-bottom: 20px;
  font-size: 24px;
}

button {
  padding: 14px 28px;
  font-size: 18px;
  background-color: #3498db; /* Dodger Blue */
  border: 2px solid #3498db; /* Dodger Blue */
  color: #ecf0f1; /* Clouds */
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2980b9; /* Belize Hole */
}

.otaku-results {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.otaku-info {
  position: relative;
  margin-right: 20px;
  margin-bottom: 20px;
  top: 120px;
  border: 2px solid #3498db; /* Dodger Blue */
  border-radius: 10px;
}

.otaku-info img {
  height: 300px;
  width: auto;
  border-radius: 10px;
  cursor: pointer;
}

.otaku-info p {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 95%;
  background: rgba(0, 0, 0, 0.5);
  color: #ecf0f1;
  text-align: center;
  padding: 5px;
  margin: 0;
  font-size: 14px;
  transition: color 0.3s ease;
}

.otaku-info p:hover {
  color: #e74c3c; /* Alizarin */
}

.otaku-chapter-container {
  font-family: 'Arial', sans-serif;
  background-color: #fff; /* White */
  padding: 20px;
  border-radius: 10px;
  margin: 20px auto;
  max-width: 80%;
}

.otaku-heading {
  color: #3498db; /* Dodger Blue */
}

.otaku-content-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}

.otaku-manga-image-container {
  flex: 1;
}

.otaku-manga-page-img {
  max-width: 100%;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.otaku-manga-details-container {
  flex: 2;
  margin-left: 20px;
}

.otaku-select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #3498db; /* Dodger Blue */
  border-radius: 5px;
  background-color: #fff;
  color: #333;
  outline: none;
}

.otaku-option {
  color: #333;
}

.otaku-manga-details {
  margin-top: 20px;
}

.otaku-manga-details h3 {
  color: #3498db; /* Dodger Blue */
  margin-bottom: 10px;
}

.otaku-manga-details p {
  color: #555;
}

/* Updated styles for genres and status */
.otaku-genres,
.otaku-status {
  display: inline-block;
  padding: 5px 10px;
  margin: 5px 0;
  border: 1px solid #3498db; /* Dodger Blue */
  border-radius: 5px;
  color: #3498db; /* Dodger Blue */
}

.otaku-selected-chapter {
  margin-top: 10px;
}

.navbar {
  background-color: #3498db;
  height: 100vh;
  width: 80px;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  margin-top: 100px;
}

.nav-link {
  text-decoration: none;
  color: #ecf0f1;
  font-size: 18px;
  margin: 15px 0;
  width: 100%;
  text-align: center;
  transition: background-color 0.3s ease;
}

.nav-link:hover {
  background-color: #2980b9;
}

.page-content {
  margin-left: 80px;
  padding: 20px;
}

/* Search Navigation Bar */
.search-navbar {
  background-color: #fff;
  height: 60px;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px;
  z-index: 1;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}


.search-title {
  color: #3498db;
  font-size: 24px;
  margin: 0;
}

.search-button {
  position: fixed;
  padding: 10px;
  font-size: 12px;
  border: none;
  right: 485px;
  top: 33px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-button:hover {
  background-color: #2980b9;
}
.async-search
{
  right: 500px;
}
/* Responsive Styles */
@media only screen and (max-width: 600px) {
  .otaku-input,
  .otaku-select,
  .otaku-button,
  .otaku-fetch-button {
    width: 100%;
  }
}